/* Taxonomy Container - start */
.taxonomyContainer {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: clip;
}
.taxonomyContainer > .taxonomy {
  border: none;
  background: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: inline-block;
  width: auto;
  vertical-align: middle;
  margin-right: 10px;
  padding: 0;
}
.taxonomyContainer > .taxonomy:nth-last-child(1) {
  margin-right: 0;
}
.taxonomyContainer > .taxonomy > a,
.taxonomyContainer > .taxonomy > p {
  font-family: 'Univers LT Roman';
  font-size: 13px;
  line-height: 1;
  letter-spacing: 0;
  color: #727272;
  border: 1px solid #727272;
  border-radius: 5px;
  padding: 0.5em;
  text-transform: uppercase;
  transition: color 200ms ease;
  position: relative;
  position: relative;
  z-index: 2;
  transition: color 200ms ease, border 200ms ease;
  display: inline-block;
}
.taxonomyContainer > .taxonomy > a:hover {
  color: #bfbfbf;
  border-color: #bfbfbf;
}
/* Taxonomy Container - end */

/* Geometric Background - start */
.geometricBackground {
  height: 150px;
  position: relative;
  z-index: -1;
}
.geometricBackground > .background {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  aspect-ratio: 4 / 1;
  background-position: center bottom;
  background-size: cover;
  background-repeat: no-repeat;
}
/* Geometric Background - end */

/* Rounded Button - start */
.roundedButton {
  border: 1px solid #000;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: none;
  border-radius: 30px;
  min-width: 150px;
  transition: background 200ms ease, opacity 200ms ease;
  padding: 0;
  cursor: pointer;
  opacity: 1;
  position: relative;
}
.roundedButton.hidden {
  opacity: 0;
}
.roundedButton.loading {
  opacity: 0.5;
}
.roundedButton::before {
  content: 'loading...';
  opacity: 0;
  padding: 1em;
  font-size: 16px;
  line-height: 1.1;
  letter-spacing: 0.01em;
  font-family: 'Gza';
  display: block;
  transition: opacity 200ms ease;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  color: #000;
}
.roundedButton > p,
.roundedButton > a {
  opacity: 1;
  padding: 1em;
  font-size: 16px;
  line-height: 1.1;
  letter-spacing: 0.01em;
  font-family: 'Gza';
  display: block;
  transition: color 200ms ease, opacity 200ms ease;
  position: relative;
}
.roundedButton.loading::before {
  opacity: 1;
}
.roundedButton.loading > p,
.roundedButton.loading > a {
  opacity: 0;
}
.roundedButton.light {
  background-color: #fff;
}
.roundedButton.light:hover {
  background-color: #000;
}
.roundedButton.light.loading:hover {
  background-color: #fff;
}
.roundedButton.light > p,
.roundedButton.light > a {
  color: #000;
}
.roundedButton.light:hover > p,
.roundedButton.light:hover > a {
  color: #fff;
}
.roundedButton.light.loading:hover > p,
.roundedButton.light.loading:hover > a {
  color: #000;
}
/* Rounded Button - end */

