/* Header - start */
header.header-liveCoverage {
  margin: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 100vw;
  z-index: 12;
  transition: height 200ms ease, background 200ms ease;
  background-color: #000;
}
header.header-liveCoverage.fixed {
  position: fixed;
  background-color: #000;
}
header.header-liveCoverage > .outer {
  max-width: 1920px;
  padding: 0px 50px;
  margin: 0 auto;
  background-color: #000;
}
header.header-liveCoverage.light > .outer {
  background-color: #fff;
}
header.header-liveCoverage > .outer > .inner {
  display: grid;
  align-content: center;
  justify-content: center;
  align-items: center;
  grid-template-columns: 100px 1fr auto;
  grid-template-areas: 'home navigation account';
  grid-gap: 20px;
  padding: 20px 0;
}
header.header-liveCoverage > .outer > .inner > .home {
  background: none;
  padding: 0;
  border: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
header.header-liveCoverage > .outer > .inner > .home > a {
  display: block;
  width: 100%;
  /* aspect-ratio: 16 / 9; */
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions {
  grid-area: navigation;
  padding: 0;
  margin: 0;
  display: grid;
  align-content: center;
  justify-content: center;
  align-items: center;
  grid-gap: 40px;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption {
  position: relative;
  height: 100%;
  cursor: pointer;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > p,
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > a {
  font-family: 'Gza';
  font-size: 14px;
  line-height: 1.1;
  letter-spacing: 0.01em;
  color: #fff;
  cursor: pointer;
  padding: 20px 0;
  border-bottom: 2px solid rgba(0, 0, 0, 0);
  transition: border 200ms ease;
  display: block;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption.active > p,
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption.active > a,
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption:hover > p,
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption:hover > a,
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption.activeNav > p,
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption.activeNav > a {
  border-bottom-color: #fc4f00;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > .submenu {
  position: absolute;
  top: 100%;
  left: 0;
  max-width: 200px;
  width: 100vw;
  overflow: hidden !important;
  height: 0;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > .submenu {
  padding: 0 10px;
  transform: translateX(-10px);
  background-color: #000;
  border-radius: 10px;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption:hover > .submenu {
  height: auto;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > .submenu > ul {
  padding: 10px 0;
  margin: 0;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > .submenu > ul > li {
  padding: 10px 10px 10px 0;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > .submenu > ul > li > a {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-family: 'Gza';
  font-size: 14px;
  line-height: 1.4;
  letter-spacing: 0.01em;
  color: #fff;
  cursor: pointer;
  overflow: hidden;
  transition: color 200ms ease;
}
header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > .submenu > ul > li:hover > a {
  color: #fc4f00;
}
header.header-liveCoverage > .outer > .inner > .accountOptions {
  grid-area: account;
  padding: 0;
  margin: 0;
  display: grid;
  grid-gap: 35px;
  align-content: center;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(2, auto);
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption {
  width: 20px;
  height: 100%;
  padding: 15px 0;
  border-bottom: 2px solid rgba(0, 0, 0, 0);
  transition: border 200ms ease;
  cursor: pointer;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption > a > svg,
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption > p > svg {
  width: 20px;
  display: block;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton {
  display: none;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton {
  background: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  cursor: pointer;
  width: 100%;
  height: 21px;
  padding: 0;
  position: relative;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span {
  display: block;
  width: auto;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 5px;
  width: 100%;
  transition: transform 200ms ease;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:before,
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background-color: #fff;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:before {
  transform: translateY(-7px);
  width: 80%;
  margin-left: 20%;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:after {
  transform: translateY(5px);
}
header.header-liveCoverage.active > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span {
  transform: translateY(-50%) rotate(45deg);
}
header.header-liveCoverage.active > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:before {
  display: none;
}
header.header-liveCoverage.active > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:after {
  transform: translateY(0px) rotate(90deg);
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.active,
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption:hover {
  border-bottom-color: #fc4f00;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.active.empty,
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.empty:hover {
  border-bottom-color: rgba(0, 0, 0, 0);
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.full > a {
  position: relative;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.full > a > svg > .Rectangle {
  display: block;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.full > a > .cartCount {
  position: absolute;
  top: 9px;
  left: 50%;
  font-size: 13px;
  line-height: 1;
  letter-spacing: 0;
  font-family: 'Univers LT Roman';
  font-weight: bold;
  color: #fff;
  z-index: 1;
  transform: translateX(-50%);
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.empty > p > svg > .Rectangle {
  display: none;
}
header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.empty > p > .cartCount {
  display: none;
}

header.header-liveCoverage.light {
  background-color: #fff;
}

header.header-liveCoverage.light > .outer > .inner > .navigationOptions > .navigationOption > p,
header.header-liveCoverage.light > .outer > .inner > .navigationOptions > .navigationOption > a {
  color: #000;
}
header.header-liveCoverage.light > .outer > .inner > .navigationOptions > .navigationOption > .submenu {
  background-color: #efefef;
}
header.header-liveCoverage.light > .outer > .inner > .navigationOptions > .navigationOption > .submenu > ul > li > a {
  color: #000;
}
header.header-liveCoverage.light > .outer > .inner > .navigationOptions > .navigationOption > .submenu > ul > li:hover > a {
  color: #fc4f00;
}
header.header-liveCoverage.light > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span {
  background-color: #000;
  top: 8px;
}
header.header-liveCoverage.light > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:before,
header.header-liveCoverage.light > .outer > .inner > .accountOptions > li.accountOption.menuButton > .headerMobileMenuButton > span:after {
  background-color: #000;
}
header.header-liveCoverage.light > .outer > .inner > .accountOptions > li.accountOption.full::before {
  top: 24px;
}
header.header-liveCoverage.light > .outer > .inner > .accountOptions > li.accountOption.full > a > .cartCount {
  top: 12px;
}

@media (max-width: 1080px) {
  header.header-liveCoverage > .outer {
    padding: 0 20px;
  }
}

@media (max-width: 800px) {
  header.header-liveCoverage {
    /* position: relative; */
    background-color: #000;
  }
  header.header-liveCoverage.active {
    position: fixed;
    height: 100vh !important;
  }
  header.header-liveCoverage > .outer > .inner {
    grid-template-areas:
      'home account'
      'navigation navigation';
    justify-content: space-between;
    grid-gap: 0px;
  }
  header.header-liveCoverage > .outer > .inner > .navigationOptions {
    height: 0;
    overflow: hidden !important;
    display: block;
    padding-top: 0px;
  }
  header.header-liveCoverage.active > .outer > .inner > .navigationOptions {
    height: calc(100vh - 98px);
    padding-top: 20px;
  }
  header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption {
    height: auto;
  }
  header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption > .submenu {
    position: relative;
    padding: 0;
    background-color: unset;
    transform: unset;
    border-radius: 0px;
  }
  header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption.active > .submenu,
  header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption.active:hover > .submenu {
    height: auto;
    max-width: unset;
    width: 100%;
    padding-left: 20px;
  }
  header.header-liveCoverage > .outer > .inner > .navigationOptions > .navigationOption:hover > .submenu {
    height: 0;
  }
  header.header-liveCoverage > .outer > .inner > .accountOptions {
    grid-template-columns: repeat(3, auto);
    justify-content: end;
    grid-gap: 25px;
  }
  header.header-liveCoverage > .outer > .inner > .accountOptions > li.accountOption.menuButton {
    display: block;
  }
}
