section.SubscriptionForm > .outer > .inner > .form > .inner > .emailContainer.error > input {
  border-color: red;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .emailContainer > .errorText {
  font-family: 'Gza';
  font-size: 12px;
  line-height: 1.1;
  letter-spacing: 0;
  color: #fff;
  padding: 16px 32px;
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  transform: translateY(100%);
  opacity: 0;
  transition: opacity 200ms ease;
  overflow: hidden;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .emailContainer.error > .errorText {
  opacity: 1;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer {
  grid-area: emailType;
  position: relative;
  cursor: pointer;
  background-color: #fff;
  border-radius: 30px;
