  border: 1px solid #ffffff;
  transition: all 200ms ease;
  z-index: 2;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer.error {
  border-color: red;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > p {
  font-family: 'Gza';
  font-size: 16px;
  line-height: 1.1;
  letter-spacing: 0;
  color: #000;
  padding: 0;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden !important;
  transition: color 200ms ease;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > p::after {
  content: '';
  position: absolute;
  right: 15px;
  top: 15px;
  width: 20px;
  height: 20px;
  background-image: url('https://www.sokito.com/wp-content/themes/sokito/assets/images/chevron-down.svg');
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  transform: rotate(0deg);
  transition: transform 200ms ease;
  z-index: 2;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > .errorText {
  font-family: 'Gza';
  font-size: 12px;
  line-height: 1.1;
  letter-spacing: 0;
  color: red;
  padding: 16px 32px;
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  transform: translateY(100%);
  opacity: 0;
  transition: opacity 200ms ease;
  overflow: hidden;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer.error > .errorText {
  opacity: 1;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer.active > p::after {
  transform: rotate(180deg);
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > ul {
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  font-family: 'Gza';
  font-size: 16px;
  line-height: 1.1;
  letter-spacing: 0;
  color: #000;
  padding: 0 2rem;
  border-radius: 30px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  background-color: #fff;
  list-style: none;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
  opacity: 0;
  transition: height 200ms ease, padding 200ms ease, opacity 200ms ease;
  overflow: hidden;
  box-sizing: content-box;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > ul.active {
  padding: 1em 2rem;
  opacity: 1;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > ul > li {
  font-family: 'Gza';
  font-size: 16px;
  line-height: 1.1;
  letter-spacing: 0;
  color: #000;
  padding: 0.5em 0;
  border-bottom: 1px solid #ebebeb;
  display: inline-block;
  width: calc(100% - 30px);
  padding-left: 30px;
  position: relative;
  cursor: pointer;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > ul > li::before {
  position: absolute;
  left: 0;
  content: '';
  width: 20px;
  height: 20px;
  display: inline-block;
  vertical-align: baseline;
  border: 1px solid #ebebeb;
  border-radius: 5px;
  margin: 0 auto;
  margin-right: 10px;
  background-image: url('https://www.sokito.com/wp-content/themes/sokito/assets/images/tick-icon.svg');
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: #fff;
  transition: background 200ms ease;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .dropdownContainer > ul > li.active::before {
  background-color: #fc4f00;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .submit {
  grid-area: submit;
  font-family: 'Gza';
  font-size: 18px;
  font-weight: bold;
  line-height: 1.3;
  letter-spacing: 0.05em;
  color: #fff;
  padding: 1rem 2rem;
  border-radius: 30px;
  margin: 0;
  border: none;
  background-color: #fc4f00;
  position: relative;
  opacity: 1;
  transition: opacity 200ms ease;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .submit::after {
  content: 'Subscribing...';
  font-family: 'Gza';
  font-size: 18px;
  font-weight: bold;
  line-height: 1.3;
  letter-spacing: 0.05em;
  color: #fff;
  padding: 1rem 2rem;
  border-radius: 30px;
  margin: 0;
  border: none;
  background-color: #fc4f00;
  position: absolute;
  opacity: 0;
  transition: opacity 200ms ease;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .submit.loading {
  cursor: not-allowed;
  opacity: 0.5;
}
section.SubscriptionForm > .outer > .inner > .form > .inner > .submit.loading::after {
  opacity: 1;
  cursor: not-allowed;
}

@media (max-width: 1200px) {
